@media (min-width: 280px) and (max-width: 640px) {
  .banner {
    .container {
      .all-backgrounds {
        background-position: 0 90px, -100px 0, center -90px;
        animation: change-background-2 6s ease-in-out infinite;
      }

      .content {
        padding: 10px 14px;
        position: relative;

        .logo {
          padding: 6px 6px;
          animation: delete-logo 6s ease-in-out infinite;
        }

        .center-block {
          position: absolute;
          left: 14px;
          display: flex;
          align-items: flex-end;
          opacity: 0;
          animation: center-appear 6s ease-in-out infinite;

          .august {
            position: relative;
            width: 54px;
            height: 29px;
            bottom: 2px;
            opacity: 0;
            margin-right: 0;
            transform: translateX(-10%);
            animation: move-august 6s ease-in-out infinite;
          }

          .text {
            position: relative;
            font-size: 14px;

            span {
              font-size: 30px;
            }

            .for-you {
              font-size: 19px;
            }
          }
        }

        .text-container {
          position: absolute;
          top: 18px;
          right: 16px;
          opacity: 0;
          animation: right-appear 6s infinite ease-in-out;
          justify-content: flex-start;
          .gulliver {
            width: 40px;
            height: 9px;
          }
          .kyiv {
            width: 17px;
            height: 6px;
          }
        }

        .btn {
          margin-top: 20px;
          font-size: 10px;
          opacity: 0;
          padding: 8px 14px;
          animation: scale-btn-2 6s infinite ease-in-out;
        }
      }
    }
  }
}

@keyframes delete-logo {
  0% {
    opacity: 1;
  }
  35%, 100% {
    opacity: 0;
  }
}

@keyframes move-august {
  0%, 40% {
    transform: translateX(30%);
    opacity: 0;
  }
  45%, 100% {
    opacity: 0.4;
    transform: translateX(-10%);
  }
}

@keyframes center-appear {
  0%, 32% {
    opacity: 0;
  }
  33%, 100% {
    opacity: 1;
  }
}

@keyframes change-background-2 {
  0%, 50% {
    background-image: url(../images/girl-2.png);
    background-repeat: no-repeat;
    background-position: -100px 0;
  }
  51%, 100% {
    background-image: url(../images/mini-girl.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }
}

@keyframes right-appear {
  0%, 50% {
    opacity: 0;
  }
  51%, 100% {
    opacity: 1;
  }
}
@keyframes scale-btn-2{
  0%,50% {
    opacity: 0;
  }
  51% {
    opacity: 1;
  }
  70% {
    transform: scale(1);
  }
  75% {
    transform: scale(0.7);
  }
  80% {
    transform: scale(1);
  }
  85% {
    transform: scale(0.7);
  }
  90% {
    transform: scale(1);
  }
  95% {
    transform: scale(0.7);
  }
  99% {
    transform: scale(1);
    opacity: 1;
  }

}
